.App {
  text-align: center;
}

body{
  background-color: lightgray;
  background-size: cover;
  background-repeat: no-repeat;
}

/*NAVIGATION*/

.navigation{
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  background-color: white;
  position: relative;
  top: -20px;
  border-bottom: 1px solid lightseagreen;
}

li{
  margin: 10px;
  color: white;
  width: 100px;
  height: 20px;
  list-style-type: none;
  text-align: center;
  text-decoration: none;
}

/*HOME*/

.containerhome{
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  position: relative;
  top:100px;
  color: darkcyan;
}

.welcome{
  text-align: center;
  font-size: 150px;
  font-style: italic;
  font-weight: lighter;
}

/*TABLE*/

h1{
  font-size: 20px;
  text-align: center;
  color: dimgray
}

.dataTable{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

table.smallTable{
  width: 400px;
}

table, th, td {
  border: 1px solid darkcyan;
  border-collapse: collapse;
  text-align: center;
 
}

th,td{
  min-width: 80px;
  padding: 5px;
}

th{
  color: darkcyan;
}

table{
  margin: 20px;
}
